"use client"

import type React from "react"
import { useState, useEffect } from "react"
import Image from "next/image"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Pencil, Trash2, Plus } from "lucide-react"
import galleryData from "@/src/data/gallery.json"

interface GalleryItem {
  id: number
  src: string
  caption: string
}

export function AdminGallery() {
  const [images, setImages] = useState<GalleryItem[]>([])
  const [editingImage, setEditingImage] = useState<GalleryItem | null>(null)
  const [isAdding, setIsAdding] = useState(false)
  const [formData, setFormData] = useState<Omit<GalleryItem, "id">>({
    src: "",
    caption: "",
  })

  useEffect(() => {
    // Load images from localStorage or use default data
    const savedImages = localStorage.getItem("galleryImages")
    if (savedImages) {
      setImages(JSON.parse(savedImages))
    } else {
      setImages(galleryData.images)
    }
  }, [])

  const saveImages = (newImages: GalleryItem[]) => {
    setImages(newImages)
    localStorage.setItem("galleryImages", JSON.stringify(newImages))
  }

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()

    if (editingImage) {
      // Update existing image
      const updatedImages = images.map((img) =>
        img.id === editingImage.id ? { ...formData, id: editingImage.id } : img,
      )
      saveImages(updatedImages)
      setEditingImage(null)
    } else {
      // Add new image
      const newImage: GalleryItem = {
        ...formData,
        id: Math.max(...images.map((i) => i.id), 0) + 1,
      }
      saveImages([...images, newImage])
      setIsAdding(false)
    }

    resetForm()
  }

  const handleEdit = (image: GalleryItem) => {
    setEditingImage(image)
    setIsAdding(false)
    setFormData({
      src: image.src,
      caption: image.caption,
    })
  }

  const handleDelete = (id: number) => {
    if (confirm("Are you sure you want to delete this image?")) {
      saveImages(images.filter((img) => img.id !== id))
    }
  }

  const resetForm = () => {
    setFormData({
      src: "",
      caption: "",
    })
  }

  return (
    <div className="space-y-8">
      {/* Header */}
      <div className="flex items-center justify-between">
        <h2 className="text-2xl font-bold text-black">Manage Gallery</h2>
        {!isAdding && !editingImage && (
          <Button
            onClick={() => {
              setIsAdding(true)
              resetForm()
            }}
            className="bg-[#FFD700] text-black hover:bg-[#E6C200] font-medium"
          >
            <Plus className="h-4 w-4 mr-2" />
            Add New Image
          </Button>
        )}
      </div>

      {/* Form */}
      {(isAdding || editingImage) && (
        <form onSubmit={handleSubmit} className="bg-white border border-gray-200 rounded-lg p-6 space-y-4">
          <h3 className="text-lg font-semibold text-black mb-4">{editingImage ? "Edit Image" : "Add New Image"}</h3>

          <div className="space-y-2">
            <Label htmlFor="src" className="text-black font-medium">
              Image URL
            </Label>
            <Input
              id="src"
              required
              value={formData.src}
              onChange={(e) => setFormData({ ...formData, src: e.target.value })}
              placeholder="/description.jpg"
              className="bg-white border-black"
            />
          </div>

          <div className="space-y-2">
            <Label htmlFor="caption" className="text-black font-medium">
              Caption
            </Label>
            <Input
              id="caption"
              required
              value={formData.caption}
              onChange={(e) => setFormData({ ...formData, caption: e.target.value })}
              className="bg-white border-black"
            />
          </div>

          {formData.src && (
            <div className="relative w-full h-48 rounded-lg overflow-hidden border border-gray-200">
              <Image src={formData.src || "/placeholder.svg"} alt="Preview" fill className="object-cover" />
            </div>
          )}

          <div className="flex gap-4">
            <Button type="submit" className="bg-[#FFD700] text-black hover:bg-[#E6C200] font-medium">
              {editingImage ? "Update Image" : "Add Image"}
            </Button>
            <Button
              type="button"
              variant="outline"
              onClick={() => {
                setIsAdding(false)
                setEditingImage(null)
                resetForm()
              }}
              className="border-black text-black hover:bg-gray-100"
            >
              Cancel
            </Button>
          </div>
        </form>
      )}

      {/* Images Grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
        {images.map((image) => (
          <div key={image.id} className="bg-white border border-gray-200 rounded-lg overflow-hidden">
            <div className="relative aspect-[4/3]">
              <Image src={image.src || "/placeholder.svg"} alt={image.caption} fill className="object-cover" />
            </div>
            <div className="p-3">
              <p className="text-sm text-gray-700 line-clamp-2 mb-3">{image.caption}</p>
              <div className="flex gap-2">
                <Button
                  size="sm"
                  onClick={() => handleEdit(image)}
                  className="flex-1 bg-[#FFD700] text-black hover:bg-[#E6C200]"
                >
                  <Pencil className="h-4 w-4" />
                </Button>
                <Button size="sm" variant="destructive" onClick={() => handleDelete(image.id)} className="flex-1">
                  <Trash2 className="h-4 w-4" />
                </Button>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  )
}
